import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react';
import { Note, playFullGame, playMajorScale, allNotes, pitchToNumber } from './Note.tsx'

function App() {
  const [scaleNote, setScaleNote] = useState(36);
  const [games, setGames] = useState(3)

  return (
    <div className="App">
      <h1>Alex's Ear Training Gym</h1>
      <ul className="gameButtons">
        <div className="game">
          <button onClick={()=>playMajorScale(pitchToNumber(scaleNote))}>
            Play major scale
          </button>
          <div class="gameOptions">
            Scale:
            <select value={scaleNote} onChange={(note) => setScaleNote(note.target.value)}> 
              {allNotes.map((note) => {
                return <option name={note}> {note.substring(0, note.length)}</option>
              })}
            </select>
          </div>
        </div>
        <div className="game">
          <button onClick={()=>playFullGame(games)}>
            Play game
          </button>
          <div class="gameOptions">
            # of games:
            <input name="games" type="number" value={games} onChange={(e) => setGames(e.target.value)}/>
          </div>
        </div>
      </ul>
      <div class="note-container">
        {[
          "C3", "C#3", "D3", "Eb3", "E3", "F3", "F#3", "G3", "Ab3", "A3", "Bb3", "B3", 
          "C4", "C#4", "D4", "Eb4", "E4", "F4", "F#4", "G4", "Ab4", "A4", "Bb4", "B4", 
          "C5"
        ].map((s) => {
            return <Note pitch={s}/>
        })}
      </div>
    </div>
  );
}

export default App;
