import React from "react";
import * as Tone from "tone"

export function Note(props: {pitch: string, isBlack: boolean}) {
    let contains = props.pitch.includes("b") || props.pitch.includes("#")
    return (
      <button className={"note " + (contains ? "black" : "white")} onClick={()=>{playTone(props.pitch)}}>
        {props.pitch}
      </button>
    );
}

export function playFullGame(reps: number) {
  for(let i = 0; i < reps; i++) {
    setTimeout(() => {
      playGame()
    }, i*7500);
  }
}

export const allNotes = ["C","C#","D","Eb","E","F","F#","G","G#","A","Bb","B"]

let allIntervals = [
  "octave",
  "minor second",
  "major second", 
  "minor third", 
  "major third", 
  "perfect fourth",
  "tri tone",
  "perfect fifth",
  "minor sixth",
  "major sixth",
  "minor seventh",
  "major seventh",
]

function playTone(note: string, length: string = "8n") {
  // alert("playing?")
  const synth = new Tone.Synth().toDestination();
  //play a middle 'C' for the duration of an 8th note
  synth.triggerAttackRelease(note, length);
}

function playToneFromNumber(tone: number, length: string = "8n") {
  playTone(numberToPitch(tone % 12) + "" + Math.floor(tone / 12), length)
}

let major = [0, 2, 4, 5, 7, 9, 11, 12]

function numberToPitch(base: number) {
  return allNotes[base % 12]
}

export function pitchToNumber(pitch: string) {
  return allNotes.indexOf(pitch)
}

export function playMajorScale(note: number) {
  let start = 36 + note

  major.forEach((interval, index) => {
    let note = interval + start
    setTimeout(() => {
      playToneFromNumber(note)
    }, index*300);
  })
}

function getRandom(options: number) {
  return Math.floor(Math.random() * options)
}

function playGame() {
  let interval = getRandom(12) + 1
  let starting = getRandom(24) + 32
  playToneFromNumber(starting, "2n")
  setTimeout(() => {
    playToneFromNumber(starting + interval, "2n")
  }, 1500);
  setTimeout(() => {
    playToneFromNumber(starting, "2n")
    playToneFromNumber(starting + interval, "2n")
  }, 3000)
  setTimeout(() => {
      var msg = new SpeechSynthesisUtterance();
      msg.text = allIntervals[interval % 12];
      window.speechSynthesis.speak(msg);
  }, 5000);
}